body {
  color: #333;
  background-color: #f9f9f9;
  margin: 0;
  padding: 20px;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

#product-container {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  display: grid;
}

.product-card {
  background-color: #fff;
  border-radius: 8px;
  padding: 15px;
  transition: transform .2s, box-shadow .2s;
  box-shadow: 0 2px 8px #0000001a;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 12px #00000026;
}

.product-title {
  color: #222;
  margin: 0 0 10px;
  font-size: 1.2em;
}

.product-image {
  object-fit: cover;
  border-radius: 4px;
  width: 100%;
  height: 150px;
  margin-bottom: 10px;
}

.product-stock, .product-discounted-price {
  margin: 8px 0;
  font-size: .9em;
}

.add-to-cart {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
  font-size: 1em;
  transition: background-color .2s;
}

.add-to-cart:hover {
  background-color: #0056b3;
}

#filter-container {
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 8px;
  margin: 20px 0;
  padding: 10px;
}

#filter-container select, #filter-container input, #filter-container button {
  margin: 5px;
  padding: 8px;
  font-size: 16px;
}
